@import '~@adjust/adjust-style/scss/_variables';
@import '~@adjust/adjust-style/scss/themes/adjust-dark/_variables';

.dropdown {
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.dropdown-button {
  margin: 0;
  border-radius: 0;
  padding: 0;
  text-transform: none;
  background-color: initial;
  color: initial;

  &:focus,
  &:hover {
    background-color: initial;
    color: initial;
  }
}

.dropdown-menu {
  display: none;
  position: absolute;
  background-color: $color-content;
  min-width: 144px;
  overflow: auto;
  box-shadow: 2px 5px 4px 0px rgba(0,0,0,0.2);
  z-index: $z-top + 1;

  &.show {
    display: block;
  }
}

.dropdown-item {
  font-size: 14px;
  line-height: 18px;
  color: white;
  padding: 12px 20px;
  display: block;

  &:hover {
    background-color: $color-content-alt;
  }
}
